import Vue from 'vue'
import Router from 'vue-router'
//1:引入组件
import Index from './components/tabbar/index.vue'
import Login from './components/tabbar/login.vue'
import Know from './components/tabbar/know.vue'
import Classify from './components/tabbar/classify.vue'
import Story from './components/tabbar/story.vue'
import storyDetail from './components/tabbar/storyDetail.vue'
import knowInfo from './components/tabbar/knowInfo.vue'
import Course from './components/tabbar/course.vue'
import practice from './components/tabbar/practice.vue'
import smCourse from './components/tabbar/smCourse.vue'
import special from './components/tabbar/special.vue'
import query from './components/tabbar/query.vue'
import cart from './components/tabbar/cart.vue'
Vue.use(Router)
//2:为组件配置访问路径 /Login
export default new Router({
  routes: [
    {path:'/',component:Index},
    {path:'/index',component:Index},
    {path:'/login',component:Login},
    {path:'/know',component:Know},
    {path:'/story',component:Story},
    {path:'/knowInfo',component:knowInfo},
    {path:'/storyDetail',component:storyDetail},
    {path:'/classify',component:Classify},
    {path:'/query',component:query},
    {path:'/cart',component:cart},
    {path:'/course',component:Course,children:[
      { path:"/", component: practice },
      { path:"/practice", component: practice },
      { path:"/smCourse", component: smCourse },
      { path:"/special", component: special }
    ]}
  ]
})


