<template>
    <div id="app-login">
       <div class="top">
       </div>
       <h3>凯叔讲故事</h3>
       <p>新人登录即可领取成长礼包</p>
       <div class="login">
        <input type="text" placeholder="请输入你的用户名" v-model="uname">
        <input type="password" placeholder="输入6-8位的密码" v-model="upwd" @blur="bg">
        <mt-button  @click="btnLogin" :style="btnBgCl">立即登录</mt-button>
        <p>我已阅读并接受《用户服务协议》和《用户隐私协议》</p>
       </div>
    </div>
</template>
<script>
//引入mint-ui Toast局部使用
import {Toast} from "mint-ui"
export default {
    data(){
        return {
            uname:"",
            upwd:"",
            btnBgCl:{background:"#b2ca96"},
        }
    },
    methods:{
        bg(){
            
            this.btnBgCl={background:"#5d8134"}  
        },
        btnLogin(){
            //1.获取用户输入的用户名和密码
            //2.创建正则表达式验证用户名和密码格式是否正确
            //3.发送ajax请求
            //4.获取服务器返回结果
            //5.提示用户
           var uname=this.uname;
           var upwd=this.upwd;
           var reg=/^[a-z0-9_]{3,8}$/i;
           if(!reg.test(uname)){
               //提示信息不是alert(),用Toast()
               Toast("用户名格式不正确,请检查");
               return;
           }
           if(!reg.test(upwd)){
               Toast("密码格式不正确,请检查");
               return;
           }
           //发送ajax
           var url="login?uname="+uname+"&upwd="+upwd;
           this.axios.get(url).then(result=>{
               var res=result.data
               if(res.code==1){
                   Toast("登录成功,2s后跳转到首页");
                   setTimeout(function(){
                       location.href=`http://127.0.0.1:5050`
                   },2000);
               }else{
                   Toast("用户名或密码有误");
               }
               
           })
        }
    }
}
</script>
<style scoped>
#app-login{
    margin:0 auto;
    text-align: center;
    padding-top:70px;
    padding-bottom:120px;
}
#app-login .top{
    margin:0 auto;
    background:url(../../img/login.jpg) no-repeat center center;
    background-size:160px; 
    width:156px;
    height:98px;
    border-radius: 26%; 
}
#app-login h3{
    color:#5d8134;
    font-size: 20px;
    margin-top:20px;
}
#app-login p{
    color:#666;
}
#app-login .login{
    margin-top:40px;
}
#app-login input{
    font-size:14px;
    width:82%;
    margin-top:10px;
    border-radius: 30px;
}
#app-login .mint-button{
    margin-top:10px;
    width:82%;
    border-radius: 30px;
    background: #b2ca96;
    color:#fff;
    font-size:16px;
}
#app-login .login p{
    font-size:10px;
    margin-top:10px;
}
</style>





