<template>
    <div id="app-storyDetail">
        <div class="head">
            <h4>{{info.sname}}</h4>
            <p>{{info.ssubname}}</p>
            <div class="image">
                <img :src="info.spic" >
            </div>
            <h4>{{info.scontent}}</h4>
        </div>
         <!-- 评论 -->
        <div class="mui-card">
            <div class="mui-card-content">
                <div class="mui-card-content-inner">
                    <textarea maxlength="120" placeholder="请吐槽，最多120个字" v-model="comment"></textarea>
                    <mt-button size="large" @click="addComment">发表留言</mt-button>
                </div>
            </div>
        </div>
        <!-- 评论列表 start -->
        <div class="mui-card">
             <div class="mui-card-header">留言 <i>{{num}}</i></div>
            <div class="mui-card-content">
                <div class="mui-card-content-inner">
                    <div class="cmt-list">
                        <div class="cmt-item" v-for="(item,i) of list" :key="i">
                            <div class="c_head">
                                <div>第<i>{{i+1}}</i>楼 </div> <div>发表时间：<i>{{item.ctime|datetimeFilter}}</i></div>
                            </div>
                            <div>{{item.content}}</div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import {Toast} from "mint-ui"
export default {
    data(){
       return {
        sid:this.$route.query.sid,
        info:{},
        comment:"",
        list:[],
        pno:0,
        pageSize:5,
        num:0
       }
    },
    created(){
        this.findInfo();
        this.loadMore();
    },
    methods:{
         findInfo(){
             this.axios.get("storyInfo?sid="+this.sid).then(result=>{
                this.info=result.data.data[0];
            })
         },
         loadMore(){
            // 1:在data添加几个数据
            // 2：获取pno，pageSize
            this.pno++;
            var pageSize=this.pageSize;
            // 3:正则表达式验证pno和pageSize是否正确 
            var reg=/^[0-9]{1,}$/;
            if(!reg.test(this.pno)){
                Toast("页码格式不正确");
                return;
            }
            // 4：如果不正确提示
            //5：创建url发送ajax请求
            this.axios.get("getcomment?sid="+this.sid+"&pno="+this.pno).then(result=>{
                this.list=result.data.data;
                this.num=this.list.length;
            })
            //6：获取返回内容
            //7：保存list
        },
         addComment(){
            // 1.评论内容双向绑定
            // 2.评论按钮绑定点击事件调用addComment
            //3.获取新闻sid获取评论内容
           //4.去除评论两端空格
            //5.如果评论为空提示判断
            //6.创建postData保存新闻nid评论内容
            //7.发送post请求/addComment
            //8.获取返回结果
            //9评论成功或失败 消息
            var sid=this.sid;
            var con=this.comment;
            var size=con.trim().length;
            var ctype="story"
            if(size==0){
                Toast("内容不能为空");
                return;
            }
            var postData=this.qs.stringify({
                sid:sid,
                ctype:ctype,
                content:con.trim()
            })
            this.axios.post("addcomment",postData).then(result=>{
                Toast("添加成功");
                this.comment="";
                this.pno=0;
                this.list=[];
                this.loadMore();
            })
        }
    }
}
</script>
<style scoped>
.head{margin:0 auto;text-align: center;}
.image{width:100%;margin-top:30px;margin-bottom:30px;}
.image img{width:60%;}
button{background: #de6237;color:#fff;}
.c_head{display:flex;justify-content: space-between;border-bottom: 1px dashed #ddd;margin-top:10px;}
.mui-card-header{font-size:14px;}
i{font-style: normal;color:darkorange;}
</style>




