<template>
    <div id="app-classify">
        <h4>全部分类</h4>
        <div class="hot">
            <img src="img/classify/hot.png"><span>热门</span>
            <div class="wall">
                <div class="cla_list" v-for="(list,i) of hotList" :key="i">
                <p>{{list.fname}}</p>
                <span>{{list.count}}个</span>
                </div>
            </div>
        </div>
        <div class="hot">
            <img src="img/classify/age.png"><span>年龄</span>
            <div class="wall">
                <div class="cla_list" v-for="(list,i) of ageList" :key="i">
                <p>{{list.fname}}</p>
                <span>{{list.count}}个</span>
                </div>
            </div>
        </div>
        <div class="hot">
            <img src="img/classify/func.png"><span>功能</span>
            <div class="wall">
                <div class="cla_list" v-for="(list,i) of funcList" :key="i">
                <p>{{list.fname}}</p>
                <span>{{list.count}}个</span>
                </div>
            </div>
        </div>
        <div class="hot">
            <img src="img/classify/cla.png"><span>类型</span>
            <div class="wall">
                <div class="cla_list" v-for="(list,i) of claList" :key="i">
                <p>{{list.fname}}</p>
                <span>{{list.count}}个</span>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            hotList:[],
            ageList:[],
            funcList:[],
            claList:[]
        }
    },
    created(){
        this.hot();
        this.age();
        this.func();
        this.cla();
    },
    methods:{
        hot(){
            this.axios.get("hotList").then(result=>{
                this.hotList=result.data.data;
            })
        },
        age(){
            this.axios.get("ageList").then(result=>{
                this.ageList=result.data.data;
            })
        },
        func(){
            this.axios.get("funcList").then(result=>{
                this.funcList=result.data.data;
            })
        },
        cla(){
            this.axios.get("classList").then(result=>{
                this.claList=result.data.data;
            })
        }
    }
}
</script>
<style scoped>
#app-classify{padding:10px}
h4{margin:0 auto !important;padding-bottom:20px;text-align: center;}
.hot{margin-bottom:30px;}
.hot span{line-height:25px;padding-left:10px;font-weight:700}
.hot img{width:25px;vertical-align: bottom;}
.wall{display: flex;justify-content:space-between;flex-wrap: wrap;margin-bottom:10px;}
.cla_list{width:100px;height:33px;background: #efeff4ad;display: flex;flex-direction: column;justify-content: space-between;align-items:center;border-radius:18px;margin-top:15px;}
.cla_list p{margin:0;padding:0;font-size:12px;color:black;}
.cla_list span{font-size:10px;margin-top:-7px;padding:0;color:#666;font-weight: 400}
</style>



