<template>
    <div id="app-story">
        <div class="story_head">
        <h4>全部故事</h4>
            <div>
            <p>故事</p>
            <p>共{{num}}个故事</p>
            </div>
        </div>
        <div class="list">
        <ul class="mui-table-view mui-table-view-chevron">
            <li class="mui-table-view-cell mui-media" v-for="(list,i) of storyList" :key="i" >
                <router-link :to="'/storyDetail?sid='+list.sid" class="mui-navigate-right">
                    <img class="mui-media-object mui-pull-left" :src="list.spic">
                    <div class="mui-media-body">
                        <div class="tit">{{list.sname}}</div>
                        <div class="subtit">{{list.ssubname}}</div>
                        <!-- <p class='mui-ellipsis'><span>03:18</span><span>222</span><span>000</span></p> -->
                    </div>
                </router-link>
            </li>
        </ul>
        <div class="loadMore" @click="loadMore">加载更多</div>
        </div>
    </div>
</template>
<script>
import {Toast} from "mint-ui"
export default {
    data(){
        return {
            num:0,
            storyList:[],
            pno:0,
            pageSize:7
        }
    },
    created(){
        this.loadMore();
    },
    methods:{
         loadMore(){
            this.pno++;
            this.axios.get("getStory?pno="+this.pno+"&&pageSize="+this.pageSize).then(result=>{
                var rows=this.storyList.concat(result.data.data);
                this.storyList=rows;
                this.num=rows.length;
                if(result.data.data.length==0){
                    Toast("加载完了")
                }
            })
        }
    }
}
</script>
<style scoped>
#app-story{padding:10px;}
.story_head{position:fixed;width:100%;box-sizing: border-box;background:#fff;padding-top:30px;margin-top:-40px;padding-right:20px}
h4{text-align:center;}
.story_head>div{font-size:12px;color:#666;display: flex;justify-content: space-between;}
.story_head>div p{margin:0;}
.list{position:absolute;width:100%;top:100px;left:0;z-index:-1;width:100%;background: #fff;padding-bottom: 50px;}
.mui-table-view-cell{font-size:14px;}
.mui-media-body p{width:70%;display: flex;justify-content: space-between;align-items: center;}
.mui-media-body p span:first-child{background: url(../../img/know/time.png) no-repeat;background-position:left center;background-size:42%;padding-left:20px;font-size:12px;}
.mui-media-body p span:nth-child(2){background: url(../../img/know/zan.png) no-repeat;background-position:left center;background-size:53%;padding-left:21px;font-size:12px;}
.mui-media-body p span:nth-child(3){background: url(../../img/know/comment.png) no-repeat;background-position:left center;background-size:42%;padding-left:21px;font-size:12px;}
.subtit{font-size:12px;color:#bbb;}
.loadMore{width:25%;border:1px solid rgba(253,209,86,0.8);text-align: center;font-size:14px;margin:10px auto;border-radius: 18px;padding-top:3px;padding-bottom:3px;color:orange;}
</style>




