<template>
    <div id="app-index">
        <div class="head">
            <div class="headLeft">
              <div class="headImg"><img src="img/tom.jpg"></div>
              <span>{{uname}}</span>
            </div>
            <div class="headRight">
              <img src="../../img/search.png" @click="jump">
              <img src="../../img/wjx.png">
              <router-link to="/" v-if="login"><mt-button @click="logout">退出</mt-button></router-link>
              <router-link to="/login" v-else><mt-button>请登录</mt-button></router-link>
          </div>
        </div>
         <!-- 轮播图  mint-ui-->
        <mt-swipe :auto="4000">
            <mt-swipe-item v-for="(img,i) of imgList" :key="i"><img :src="img.img_url"></mt-swipe-item>
        </mt-swipe>
        <!-- 九宫格 mui-->
         <!-- <ul class="mui-table-view mui-grid-view mui-grid-9">
            <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3" v-for="(grid,i) of gridList" :key="i">
                <a href="#">
            <img :src="grid.img_url" :data-id="grid.lid">
            <div class="mui-media-body">{{grid.title}}</div></a>
            </li>
         </ul> -->
         <ul class="menus">
             <li class="menu"  v-for="(grid,i) of gridList" :key="i">
                 <img :src="grid.img_url" :data-id="grid.lid" @click="link">
                 <p>{{grid.title}}</p>
             </li>
         </ul>
         <div class="know">
             <img src="img/index/know.png">
             <div class="know_list">
                 <div class="know_left"><img src="img/index/book.gif"></div>
             <div class="animal">
                <p>动脑 | 什么冰没有水<span>12:00</span></p>
                <div class="animal_for">
                    <div :style="totop">
                        <p v-for="(item,i) of knowList" :key="i" :style="bgcolor"><router-link :to="'/knowInfo?kid='+item.kid">{{item.kname}}</router-link><span>{{item.ktime|datetimeFilter}}</span></p>
                    </div>
                </div>
             </div>
             </div>
            <div class="know_more"><router-link to="/know">查看更多</router-link><i>></i></div>
         </div>
         <div class="recommend">
             <div class="rec_title">
                 <h4>今日推荐</h4><router-link to="/story"><span>更多  ></span></router-link>
             </div>
             <ul class="rec_list">
                 <li v-for="(list,i) of recommendList" :key="i"><router-link :to="'/storyDetail?sid='+list.sid"><img :src="list.spic"></router-link><p>{{list.sname}}</p><span>{{list.ssubname}}</span></li>
             </ul> 
         </div>
         <div class="p_child">
             <h4>亲子训练营</h4>
             <img src="img/index/qinzi.jpg">
             <p>0基础水彩亲子课|4.9</p>
             <p>【送水彩套装】跟着绘本画水彩，拿起笔就能创作</p>
         </div>
         <!-- 分类精选 -->
        <div class="classify">
             <div class="cla_title">
                 <h4>精选分类</h4><router-link to="/classify"><span>更多  ></span></router-link>
             </div>
             <ul class="cla_list">
                 <li v-for="(list,i) of claList" :key="i"><img :src="list.fimg"><p>{{list.fname}}</p></li>
             </ul> 
         </div>
         <!-- tabbar mui底部导航-->
    <!-- <nav class="mui-bar mui-bar-tab">
        <a class="mui-tab-item mui-active" href="#tabbar">
            <span class="mui-icon mui-icon-home"></span>
            <span class="mui-tab-label">首页</span>
        </a>
        <a class="mui-tab-item" href="#tabbar-with-chat">
            <span class="mui-icon mui-icon-extra mui-icon-extra-university"><span class="mui-badge">9</span></span>
            <span class="mui-tab-label">故事</span>
        </a>
        <a class="mui-tab-item" href="#tabbar-with-contact">
            <span class="mui-icon mui-icon-extra mui-icon-extra-xiaoshuo"></span>
            <span class="mui-tab-label">课程</span>
        </a>
        <a class="mui-tab-item" href="#tabbar-with-map">
            <span class="mui-icon mui-icon-contact mui-icon-icon-contact-filled"></span>
            <span class="mui-tab-label">我的</span>
        </a>
    </nav> -->
    </div>
</template>
<script>
export default {
    data(){
        return {
            imgList:[],
            gridList:[],
            knowList:[],
            recommendList:[],
            claList:[],
            changetop:0,
            totop:"",
            bgcolor:"",
            login:false,
            uname:"凯猴子"
        }
    },
    created(){
        this.handleImage();
        this.loadSession();
        this.grid();
        this.animal();
        this.recommend();
        this.classify();
    },
    mounted(){
        this.changeTop();
    },
    methods:{
    jump(){
        this.$router.push("/query")
    },
    logout(){
        this.login=false;
        this.uname="凯猴子"
    },
    loadSession(){
        this.axios.get("loadSession").then(result=>{
            var res=result.data.data
            if(res){
                this.login=true;
                this.uname=res[0].uname;
            }else{
                this.login=false;
            }
        })
    },   
    link(e){
        var id=e.target.dataset.id;
        if(id==4){
            this.$router.push('/classify')
        }else if(id==5){
            this.$router.push('/course')
        }
    },
    classify(){
        this.axios.get("claList").then(result=>{
            this.claList=result.data.data;
        })
    },
    recommend(){
        this.axios.get("recommendList").then(result=>{
            this.recommendList=result.data.data;
        })
    },
    //加载轮播图的数据
    handleImage(){
    this.axios.get("imgList").then(result=>{
        this.imgList=result.data.data;
       })
       },
    grid(){
            this.axios.get("grid").then(result=>{
            this.gridList=result.data.data;
        })
    },
    animal(){
        this.axios.get("knowList").then(result=>{
            this.knowList=result.data.data;
        })
    },
    changeTop(){
        var t=setInterval(function(){
            var m=this.knowList.length-1
            m=m*(-25)
            this.changetop-=25;
            this.totop=`top:${this.changetop}px`
            if(this.changetop==m){
                this.changetop=0;
            }
            function rn(){
            var r=Math.ceil(Math.random()*228);
            var g=Math.ceil(Math.random()*228);
            var b=Math.ceil(Math.random()*228);
            return `color:rgb(${r},${g},${b})`
        }
        this.bgcolor=rn(); 
        }.bind(this),3000)
        // return `top:${this.changetop}px`
        
    }
  } 
}
</script>
<style scoped>
/* #app-index{padding-top:30px;width:100%;} */
.head{display: flex; justify-content: space-between;width:100%;}
.headLeft{display: flex; justify-content: start;width:50%;margin-left:10px;}
.headLeft span{line-height: 35px;font-size: 14px;margin-left:10px;color:orange;}
.headImg{width: 35px;height: 35px;border-radius: 50%;overflow: hidden;}
.headImg img{width:35px;}
.headRight{display: flex; margin-right:10px;justify-content: flex-end;width:50%;height:35px;padding-top:8px;}
.headRight img{width:20px;margin-left:20px;height:20px;}
.headRight a{width:34%;margin-right:15px;}
.headRight .mint-button{margin-left:20px;width:100%;height:20px;font-size: 14px;background: orange;color:#fff;text-align: center;border-radius: 9px;}
.mint-swipe{height:150px !important; margin-top:5px;}
.mint-swipe img{width:100%;}
ul.menus{display:flex;justify-content: space-around;padding:0;}
ul li.menu{width:15%;display: block;list-style: none;text-align: center;}
ul li.menu img{width:60%;}
ul li.menu p{font-size:12px;}
.know{width:95%;margin:0 auto;box-shadow:0px 0px 5px 1px #ddd;border-radius:7px;padding:7px;}
.know img{width:36%;}
.know_list{display: flex;justify-content: space-between;}
.know_left{width:16%}
.know_left img{width:100%;}
.animal{width:80%;margin-top:5px;}
.animal p{font-size:13px;display: flex;justify-content:space-between;padding-left:2px;padding-right:2px;line-height: 15px;color:black;height:15px;}
.animal_for{width:100%;height:15px;overflow: hidden;position: relative;}
.animal_for div p{position: relative;width:100%}
.animal_for div p span{display:block;position:absolute;right:2px;}
.animal_for div{position: absolute;top:0px;width:100%}
#app-index .mui-grid-view.mui-grid-9{background:white;}
.mui-table-view.mui-grid-view .mui-table-view-cell .mui-media-body{font-size:14px !important;}
#app-index .mui-grid-9 img{width:60px;height:60px;}
.know_more{width:100%;margin:0 auto;font-size:13px;text-align:center;color:#a29b9b;}
.know_more a{color:#a29b9b;}
.know_more i{margin-left:5px;}
.recommend{width:100%;padding:10px;padding-bottom:0}
.recommend .rec_title{display: flex;justify-content: space-between;padding-top:10px;}
.recommend .rec_title span{color:#73cc26;font-size:14px;}
.recommend .rec_title h4{font-weight:650}
.recommend ul.rec_list{list-style: none;display: flex;flex-wrap: wrap;justify-content:space-around;padding:0;}
.recommend ul.rec_list li{width:45%;padding-bottom:10px;}
.recommend ul.rec_list li img{width:100%;}
.recommend ul.rec_list li p{font-size:14px;color:black;font-weight: bold;margin:0;padding-top:6px;}
.recommend ul.rec_list li span{font-size: 12px;color:#8f8f94;margin:0}
.classify{width:100%;padding:10px;padding-top:0}
.classify .cla_title{display: flex;justify-content: space-between;}
.classify .cla_title span{color:#73cc26;font-size:14px;}
.classify .cla_title h4{font-weight:650}
.classify ul.cla_list{width:100%;list-style: none;display: flex;flex-wrap: wrap;justify-content:space-between;padding:0;}
.classify ul.cla_list li{width:30%;padding-bottom:10px;}
.classify ul.cla_list li img{width:100%;}
.classify ul.cla_list li p{font-size:14px;color:black;font-weight: bold;margin:0;padding-top:6px;text-align: center;}
.p_child{width:100%;text-align:left;padding:10px;padding-bottom:30px;}
.p_child>p{margin:0;padding:0}
.p_child>p:nth-child(3){color:black;font-weight: bold;padding-top:6px;}
.p_child>p:last-child{font-size:12px;}
.p_child img{width:100%;text-align: center;margin-top:6px;}
</style>





