<template>
   <div class="practice">
            <h4>训练营</h4>
            <div class="course_list" v-for="(list,i) of courseList" :key="i">
                <div class="left"><img :src="list.pic"></div>
                <div class="right">
                    <p>{{list.title}}</p>
                    <p>{{list.sub_title}}</p>
                    <div><span>{{list.price}}元</span><span>{{list.v_price}}元<i>年卡会员价</i></span></div>
                </div>
            </div>
        </div>
</template>
<script>
export default {
    data(){
        return{
            courseList:[]
        }

    },
    created(){
        this.getCourse()
    },
    methods:{ 
        getCourse(){
            var family="训练营"
            this.axios.get("getCourse?family="+family).then(result=>{
                this.courseList=result.data.data;
            })
            
        }
    }
}
</script>
<style scoped>
.practice{padding:10px;width:100%;}
.practice>.course_list{width:100%;display:flex;justify-content: space-between;margin-top:25px;}
.practice>.course_list .left{width:45%;border-radius: 6px;overflow: hidden;padding-right:12px;}
.practice>.course_list .left img{width:100%}
.course_list .right{width:55%;}
.course_list .right p{padding:0;margin:0}
.course_list .right p:nth-child(1){color:black;font-weight: bold;}
.course_list .right p:nth-child(2){font-size:12px;}
.course_list .right div{font-size: 13px;margin-bottom: 0}
.course_list .right div span:nth-child(1){color:orange;padding-right:20px;}
.course_list .right div span:nth-child(2){color:#ff5722;font-weight: bold;}
.course_list .right div span i{font-style: normal;background: burlywood;font-size:12px;padding:1px;color:#fff;margin-left:3px;border-radius: 5px;}
</style>



