<template>
    <div id="app-course">
        <div class="head">
            <h4>课 程</h4>
        </div>
        <mt-swipe :auto="4000">
            <mt-swipe-item><img src="img/course/banner.jpg"></mt-swipe-item>
            <mt-swipe-item><img src="img/course/banner1.jpg"></mt-swipe-item>
            <mt-swipe-item><img src="img/course/banner2.jpg"></mt-swipe-item>
        </mt-swipe>
        <ul class="menus">
             <li class="menu"  v-for="(menu,i) of menuList" :key="i" @click="change">
                 <img :src="menu.img_url" :data-id="menu.lid" :data-title="menu.title">
                 <p>{{menu.title}}</p>
             </li>
         </ul>
    </div>
</template>

<script>
export default {
    data(){
        return{
            menuList:[],
        }
    },
    created(){
        this.getMenu();
    },
    methods:{
        getMenu(){
            this.axios.get("getMenu").then(result=>{
            this.menuList=result.data.data;
        })
        },
        change(e){
            var id=e.target.dataset.id;
            if(id==1){
                this.$router.push("/practice")
            }else if(id==2){
                this.$router.push("/special")
            }else if(id==3){
                this.$router.push("/smCourse")
            }
        }
    }
}
</script>
<style>
#app-course .head h4{text-align: center;font-size:20px;padding-bottom: 5px;}
#app-course .mint-swipe{height:150px !important; margin-top:5px;}
#app-course .mint-swipe img{width:100%;}
#app-course ul.menus{display:flex;justify-content: space-around;padding:0;}
#app-course ul li.menu{width:15%;display: block;list-style: none;text-align: center;}
#app-course ul li.menu img{width:60%;}
#app-course ul li.menu p{font-size:12px;}

</style>




