<template>
    <div id="app-know">
        <div class="head">
            <div class="head_left">
                <div class="head_pic">
                    <img src="img/know/10.png">
                </div>
                <div>
                <p>凯叔小知识</p>
                <span>100人订阅</span>
                </div>
            </div>
            <div class="head_right"><mt-button>+订阅</mt-button></div>
        </div>
        <div>
            <div class="order">
            <p>已更新88条</p>
            <div><img src="img/know/desc.png">排序</div>
            </div>
        </div> 
        <div class="know_card">
            <p>2019-03-25</p>
            <mt-button>知识卡</mt-button>
        </div>
        <ul class="mui-table-view mui-table-view-chevron">
            <li class="mui-table-view-cell mui-media" v-for="(list,i) of knowList" :key="i">
                <router-link :to="'/knowInfo?kid='+list.kid" class="mui-navigate-right">
                    <img class="mui-media-object mui-pull-left" :src="list.kpic">
                    <div class="mui-media-body">
                        {{list.kname}}
                        <p class='mui-ellipsis'><span>{{list.ktime|datetimeFilter}}</span><span>{{list.kpoint}}</span><span>{{list.kcomment}}</span></p>
                    </div>
                </router-link>
            </li>
        </ul>
        <div class="loadMore" @click="loadMore">加载更多</div>
    </div>
</template>
<script>
import {Toast} from "mint-ui"
export default {
    data(){
        return {
            knowList:[],
            pno:1,
            pageSize:6
        }
    },
    created(){
        this.knowlist();
    },
    methods:{
        knowlist(){
            this.axios.get("knowList?pno="+this.pno+"&&pageSize="+this.pageSize).then(result=>{
                this.knowList=result.data.data;
            })
        },
        loadMore(){
            this.pno++;
            this.axios.get("knowList?pno="+this.pno+"&&pageSize="+this.pageSize).then(result=>{
                var rows=this.knowList.concat(result.data.data);
                this.knowList=rows;
                if(result.data.data.length==0){
                    Toast("加载完了")
                }
            })
        }
    }
}
</script>
<style scoped>
#app-know{margin-top:-30px;}
.head{width:100%;padding:10px;display: flex;justify-content: space-between;background:#fdd156;padding-top:50px;align-items: center;}
.head_left{width:50%;display: flex;justify-content:flex-start;align-items: center;}
.head_pic{width:40%;margin-right:5px;}
.head_pic+div>p{color:black;font-size:15px;padding:0;margin:0}
.head_pic+div>span{font-size:12px;}
.head_left img{width:100%;}
.mint-button--normal{width:100%;height:25px;border-radius:12px;background:#fff;color:#f10303;font-size:12px;}
.head+div{background:#fdd156 }
.order{display:flex;justify-content: space-between;align-items: center;padding:10px;background: #fff;border-top-left-radius:14px;border-top-right-radius:14px;}
.order img{width:18px;margin-right:8px;vertical-align: middle;}
.order div{font-size:15px;padding:0}
.know_card{display:flex;justify-content:space-between;align-items:center;background:#f5f5f5;padding-left:10px;padding-right:10px;}
.know_card button{height:22px;font-size:12px;width:18%;background:url(../../img/know/light.png) no-repeat;background-position:left center;background-size:33%;padding-left:23px;border:1px solid orange;color:orange; }
.know_card p{line-height:22px;padding-top:10px;}
.mui-table-view-cell{font-size:14px;}
.mui-media-body p{width:70%;display: flex;justify-content: space-between;align-items: center;}
.mui-media-body p span:first-child{background: url(../../img/know/time.png) no-repeat;background-position:left center;background-size:42%;padding-left:20px;font-size:12px;}
.mui-media-body p span:nth-child(2){background: url(../../img/know/zan.png) no-repeat;background-position:left center;background-size:74%;padding-left:21px;font-size:12px;}
.mui-media-body p span:nth-child(3){background: url(../../img/know/comment.png) no-repeat;background-position:left center;background-size:60%;padding-left:21px;font-size:12px;}
.loadMore{width:100%;background:rgba(253,209,86,0.8);text-align: center;padding:10px;font-size:16px;}
</style>





