<template>
    <div id="app-query">
    <div class="head">
        <input type="text" placeholder="请输入搜索内容" v-model="search">
        <p @click="searchC">搜索</p>
    </div>
    <div class="history">
        <p>历史搜索</p>
        <img src="img/query/del.png">
        <div class="history_list">
            <p>凯叔·诗词来了</p>
            <p>小马宝利</p>
            <p>凯叔·恐龙战士</p>
        </div>
    </div>
    <div class="hot_query">
        <p>热门搜索</p>
        <ul>
            <li>小马宝利</li>
            <li>凯叔·诗词来了</li>
            <li>凯叔·恐龙战士</li>
        </ul>
    </div>
    <div class="search_result">
    <p>搜索结果</p>
    <ul v-if="sorry" class="sorr">
        <img src="img/query/sorry.png">
        /(ㄒoㄒ)/~~很抱歉！没有找到相关内容
    </ul>
    <ul class="mui-table-view mui-table-view-chevron" v-else>
        <li class="mui-table-view-cell mui-media" v-for="(list,i) of searchList" :key="i">
            <router-link :to="'/storyDetail?sid='+list.sid" class="mui-navigate-right">
                <img class="mui-media-object mui-pull-left" :src="list.spic">
                <div class="mui-media-body">
                    <div class="tit">{{list.sname}}</div>
                    <div class="subtit">{{list.ssubname}}</div>
                </div>
            </router-link>
        </li>
    </ul>
    </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            search:"",
            searchList:[],
            sorry:false
        }
    },
    created(){
        // this.searchC();
    },
    methods:{
        searchC(){
            this.axios.get("search?subtitle="+this.search).then((result)=>{
                if(result.data.data){
                    this.searchList=result.data.data;
                    this.sorry=false;
                    this.search=""
                }else{
                    this.sorry=true;
                    this.search=""
                }  
            })
        }
    }
}
</script>
<style scoped>
.head{position: relative;}
.head p{position: absolute;right:10px;top:10px;color:#97cc35;}
input{border: 0px !important;border-bottom: 1px solid #6666664f !important;font-size:14px;color:#6666664f;padding-left:30px !important;background:url(../../img/query/query.png) no-repeat 2% center;background-size:6%;}
 input::placeholder{color:#6666664f;}
 .history{padding:10px;padding-top:0;position: relative;}
 .history img{width:21px;position: absolute;top:0;right:10px;}
 .history_list{display:flex;flex-wrap: wrap;justify-content: space-between;width:100%;}
 .history_list p{width:48%;text-align: center;background:#e6e6e84f;height:36px;line-height: 36px;border-radius:5px;}
 .hot_query{padding:10px;}
 .hot_query ul{width:100%;padding-left:0;}
 .hot_query ul li{list-style: none;font-size:13px !important;border-bottom:1px solid #d2d0d04f;padding:10px;padding-left:20px;}
 .hot_query ul li:first-child{background: url(../../img/query/hot01.png) no-repeat left center;background-size:5%;}
 .hot_query ul li:nth-child(2){background: url(../../img/query/hot03.png) no-repeat left center;background-size:5%;}
 .hot_query ul li:nth-child(3){background: url(../../img/query/hot02.png) no-repeat left center;background-size:5%;}
 .search_result{padding:10px;padding-bottom:100px;}
 .mui-table-view-cell{font-size:14px;}
 .sorry{-webkit-padding-start: 0px !important;}
 .sorr img{width:20%;display:block;margin:0 auto}
 .subtit{font-size:12px;color:#bbb;}
</style>




